<template>
  <h1 class="admin-heading" @click="goTo">
    <slot />
  </h1>
</template>

<script>
import { useRouter } from '@/shared/plugins/vue-router'

export default {
  name: 'Heading',
  props: {
    to: {
      type: [Object, String]
    }
  },
  setup (props) {
    const router = useRouter()

    const goTo = () => {
      if (props.to) {
        router.push(props.to)
      }
    }

    return {
      goTo
    }
  }
}
</script>

<style lang="scss">
.admin-heading {
  font-size: 32px;
  line-height: 37px;
  cursor: pointer;
}
</style>
